@import url(https://fonts.googleapis.com/css2?family=Monoton&family=Montserrat&display=swap);
@import url(https://maxcdn.bootstrapcdn.com/bootstrap/latest/css/bootstrap.min.css);
/* @import url('https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/css/bootstrap.min.css'); */

:root {
  --ColorDarkBlue: #28363D;
  --ColorTeal: #2F575D;
  --ColorGreen: #658B6F;
  --ColorLightBlue: #6D9197;
  --ColorTan: #dee1dd;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #28363D;
  background-color: var(--ColorDarkBlue);
  color: #dee1dd;
  color: var(--ColorTan);
}
H2 {
  font-family: 'Monoton', cursive;
  font-size: 24pt;
  font-weight: normal;
}

.menu {
  padding: 20px;
  background-color: #2F575D;
  background-color: var(--ColorTeal);
  /* display: flex; */
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.menu-name {
  font-family: 'Monoton', cursive;
  font-weight: normal;
  font-size: 28pt;
}

.menu-options{
  display: -webkit-flex;
  display: flex;
  list-style: none;
  margin-right: 25px;
}

.menu-item {
  font-size: 14pt;
  text-decoration: underline;
  display: inline-block;
  margin-left: 25px;
}

a:link {
  color: #dee1dd;
  color: var(--ColorTan);
}

/* a link the user has visited */
a:visited {
  color: #28363D;
  color: var(--ColorDarkBlue);
}

/* a link when the user hovers over it */
a:hover {
  color: #cdb3d4;
}

/* a link the moment it is clicked */
a:active {
  color: #6D9197;
  color: var(--ColorLightBlue);
}

.projectSection a:visited {
  color: #658B6F;
  color: var(--ColorGreen);
}

.projectSection a:active,
.projectSection a:hover{
  color: #6D9197;
  color: var(--ColorLightBlue);
}

.projectSection{
  display: -webkit-flex;
  display: flex;
  margin: 30px;
  -webkit-align-items: center;
          align-items: center;
}

.projectInfo{
  margin: 20px;
}

.projectPic{
  max-height: 300px;
  width: auto;
}

.projects h2{
  margin: 25px;
  font-size: 24pt;
}

.description{
  font-style: italic;
}

/* img {
  width: auto;
  display: block;
  margin: auto;
} */

.about{
  margin: 25px;
}

.education{
  margin: 25px;
  display: -webkit-flex;
  display: flex;
  border: solid;
  border-color: #658B6F;
  border-color: var(--ColorGreen);
  border-radius: 10%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.education h2,
.professional h2{
  margin-left: 20px;
  margin-right: 30px;
}

.school{
  margin: 20px;
  width: 35%;
}

.school p{
  margin: 0;
}

.schoolName{
  font-weight: bold;
  font-size: 16pt;
}

.professional{
  margin: 25px;
  display: -webkit-flex;
  display: flex;
  border: solid;
  border-color: #658B6F;
  border-color: var(--ColorGreen);
  border-radius: 10%;
  -webkit-align-items: center;
          align-items: center;
}

.workInfo{
  margin: 20px;
  /* width: 30%; */
}

.company{
  font-weight: bold;
  font-size: 16pt;
  margin: 5px;
}

.job{
  font-style: italic;
  margin: 0;
}
.main{
    margin: 10px;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
}
.menu{
  display: -webkit-flex;
  display: flex;
}

.menu-options{
  display: -webkit-flex;
  display: flex;
  list-style: none;
  margin-right: 25px;
}

.menu-item{
  font-size: 14pt;
  text-decoration: underline;
  display: inline-block;
  margin-left: 25px;
}
.wrapper {
  min-height: 100%;
  /* padding-bottom: 100px; */
}

:root {
    --ColorDarkBlue: #28363D;
    --ColorTeal: #2F575D;
    --ColorGreen: #658B6F;
    --ColorLightBlue: #6D9197;
    --ColorTan: #dee1dd;
}

.footer {
    background-color: #658B6F;
    background-color: var(--ColorGreen);
    display: -webkit-flex;
    display: flex;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
